<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-top">
        <div class="w-left w-charts">
          <ctitle :title="`${curCity}区划排名`"></ctitle>
          <div class='w-left-bt'>
            <div class='charts-leftBar'>
              <qhpmBtn @change="changeType"></qhpmBtn>
            </div>
            <div v-if="curCity!=='广东省'" class="map-back" @click="mapBack"></div>
            <div class="charts">
              <barline ref="qhBar" :params="xflphBarParams" @departmentClick="openCounty"></barline>
            </div>
          </div>
        </div>
        <div class="w-right">
          <div class="w-right-data">
            <yjzt v-if="yjztData.length" :data="yjztData"></yjzt>
          </div>
        </div>
      </div>
      <div class='c-bottom'>
          <div class="w-left">
            <ctitle :title="'信访量走势'"></ctitle>
              <lineMonth :params="zsLineParams"></lineMonth>
          </div>
          <div class="w-right">
            <div class="w-right-l">
              <ctitle :title="'内容分类分布'"></ctitle>
              <div class="inner">
                <loopPie class="chart" :params="tswtPieParams"></loopPie>
              </div>
            </div>
            <div class='w-right-r'>
              <ctitle :title="'信访目的分布'"></ctitle>
              <div class="inner">
                <pie-chart3D class="chart" :params="xfmdzbPieParams"></pie-chart3D>
              </div>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import loopPie from "@/components/pie/LoopPie";
  import barline from "@/components/bar/barLine2";
  import PieChart3D from "@/components/pie/PieChart3D";
  import lineMonth from "@/components/line/lineMonth";
  import yjzt from "@/components/yjzt/index";
  import qhpmBtn from "@/components/qhpmBtn/index";

  export default {
    name: "fsf",
    components: {
      qhpmBtn,
      ctitle,
      loopPie,
      PieChart3D,
      barline,
      lineMonth,
      yjzt
    },
    data() {
      return {
        curCity: "广东省",
        shijCity: "",
        curName: 'drop',
        yjzt:'告警',
        //2.预警状态
        yjztData:[],
        percentage:21,
        listScroll: null,
        curTypeId: "信访量",
        // 区划排名
        xflphBarParams: {
          curCity: "广东省",
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "5%",
            right: "5%",
            top: "5%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "40%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "信访量"
            }
          }
        },
        // 信访量走势
        day:'',
        xfjc:'',
        zsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: "rgba(220, 150, 209, 0.3)",//"#6F5CFF",'#FBA7D2'
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0.1)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
        //内容分类分布
        yjnr:'',
        xfjc:'',
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        //信访目的分布
        xfmd:'',
        xfmdzbPieParams: {
          data: [],
          color: this.$STANDARD.pieColors,
          angle: 20,
          radius: "30%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        mapJson: {
          "广东省": "440000",
          "广州市": "440100",
          "韶关市": "440200",
          "深圳市": "440300",
          "珠海市": "440400",
          "汕头市": "440500",
          "佛山市": "440600",
          "江门市": "440700",
          "湛江市": "440800",
          "茂名市": "440900",
          "肇庆市": "441200",
          "惠州市": "441300",
          "梅州市": "441400",
          "汕尾市": "441500",
          "河源市": "441600",
          "阳江市": "441700",
          "清远市": "441800",
          "东莞市": "441900",
          "中山市": "442000",
          "潮州市": "445100",
          "揭阳市": "445200",
          "云浮市": "445300",
        },
      };
    },
    mounted() {
      this.init(this.date);
    },
    methods: {
      // 初始化组件
      init(date) {
        this.getColumnCharts(date)
        this.getAPI(date)
      },
      // 点击左侧柱图
      changeType(id) {
        this.curTypeId = id;
        this.getColumnCharts(this.date);
      },
      // 1.区划排名
      getColumnCharts(date) {
        if (this.curCity === '广东省') {
          this.shej_02 = this.curCity;
          this.shij_02 = 'invalid';
          this.xj_02 = 'invalid';
          this.nameP = 'shij_02';
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = "广东省";
            this.shij_02 = this.shijCity;
            this.nameP = "xj_02";
            if (this.xj_02 !== 'invalid') {
              this.shej_02 = "广东省";
              this.shij_02 = this.shijCity;
              this.xj_02 = this.curCity;
            } else {
              this.shej_02 = "广东省";
              this.shij_02 = this.shijCity;
              this.xj_02 = 'invalid';
            }
          } else {
            this.shej_02 = "广东省";
            this.shijCity = 'invalid',
            this.xj_02 = 'invalid'
            this.nameP = "invalid";
          }
        }
        get_http("xfsxyjjjfsfqhpm", {
          Cqh: this.curCity,
          day: `[${date.startDate},${date.endDate}]`,
          gd_id3: this.curTypeId
        })
          .then(res => {
            this.xflphBarParams.options.map.value = this.curTypeId;
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击柱图下钻
      openCounty(name){
        this.xflphBarParams.curCity = this.curCity = name;
        let c = "";
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            c = this.curCity;
          }
        }
        if (!c) {
          this.$refs.qhBar.setHighBar();
        }
      },
      // 柱图返回全省
      mapBack() {
        this.$refs.qhBar.animation=true;
        this.curCity = '广东省';
      },
      getAPI(date) {
        // 2.预警状态
        get_http("xfsxyjfsfyjzt", {
          Cqh: this.curCity,
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.yjztData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3信访量走势
        get_http("xfsxyjfsfxxlzs", {
          Cqh: this.curCity,
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.zsLineParams.data = res.data.data;

          })
          .catch(err => {
            console.log(err);
          });
        // 4.内容分类分布
        get_http("xfsxyjfsfnrflfb", {
          Cqh: this.curCity,
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.信访目的分布
        get_http("xfsxyjfsfxfmdfb", {
          Cqh: this.curCity,
          day: `[${date.startDate},${date.endDate}]`
          })
          .then(res => {
            this.xfmdzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.init(val);
      },
      curCity: function (newVal, oldVal) {
        if (newVal === '广东省') {
          this.init(this.date)
        } else {
          let c = "";
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.init(this.date);
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI(this.date);
          }
        }
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      padding .2rem .3rem
      display flex
      flex-direction column

      .c-top
        height 50%
        position relative
        flex-direction row
        display flex

        .w-left
          flex 1.2
          display flex
          flex-direction column
          .w-left-bt
            display flex
            flex 1
            flex-direction column
            box-sizing border-box
            position relative
            .map-back
              position absolute
              top 30px
              right 60px
              width 30px
              height 30px
              background url("~@/assets/back-icon.png") no-repeat
              z-index 1000
              font-size 0.2rem
              color #fff
              cursor pointer
            .charts-leftBar
              width 80%
              margin 0 auto
              justify-content center
            .charts
              display flex
              flex 1
              justify-content center
        .w-right
          flex 1
          .w-right-data
            display flex
            height 100%
            align-items center
            flex-direction row
            .w-r-d-yjzt
              height 50%
              flex 1
              display flex
              padding .2rem 0
              justify-content center
              flex-direction column
              border-right 1px #fff dashed

              h4
                display flex
                flex 1
                margin 0 auto
                color #fff
                font-size 22px
                font-weight 600
                padding-bottom .3rem
              .no-padding
                padding 0
              .yjztDiv
                width .6rem
                height .35rem
                display flex
                margin 0 auto
                justify-content center
                border 1px rgba(255, 35, 0, 0.8) solid
                background rgba(204, 4, 4, 0.42)
                font-size 14px
                color #fff
                text-align center
                line-height 2
                border-radius .08rem
              .percent
                display flex
                flex-direction row
                font-size 14px
                color #fff
                margin 0 auto
                position relative
                p
                 padding-top .1rem
                .span1
                  width .38rem
                  height .39rem

                  background url("../../../assets/bfb-up.png") no-repeat center
                  background-size 100% 100%
                .span2
                  width .28rem
                  height .27rem
                  position absolute
                  right -.25rem
                  background url("../../../assets/bfb-down.png") no-repeat center
                  background-size 100% 100%
              div
                display flex
                align-items flex-end

            .w-r-d-yjzt:last-child
              border none

      .c-bottom
        height 50%
        position relative
        display flex
        box-sizing border-box
        .w-left
          flex 1.1

        .w-right
          flex .9
          display flex
          box-sizing border-box
          flex-direction row
          .w-right-l
            height 100%
            position relative
            flex 1
            .inner
              display flex
              height calc(100% - 0.28rem)
              padding-top 0.28rem
              .chart
                width 100%
                height 100%
          .w-right-r
            height 100%
            position relative
            flex 1
            .inner
              display flex
              height calc(100% - 0.28rem)
              padding-top 0.28rem
              .chart
                width 100%
                height 100%


</style>
